@import "../../styles/style.scss";

.ui-header {
  display: flex;
  position: relative;
  justify-content: space-between;
  max-width: 1280px;
  margin: 0 auto;
  position: fixed;
  left: 0;
  right: 0;
  transition: "top 0.5s ease-in-out";
  z-index: 100;
  font-size: toRem(30);
  a {
    color: #3f89e6;
    font-family: 'Bagel fat one';
    text-decoration: none;
  }
  .menu {
    cursor: pointer;
    display: none; /* 기본 데스크톱에서는 숨김 */
  }
  // .navi {
  //   transition: max-height 0.4s ease;
  //   overflow: hidden;
  //   max-height: none; /* 기본 데스크톱에선 제한 없음 */
  // }
  
}


// size
.ui-header-md {
  padding: toRem(36) toRem(24);
}
.ui-header-lg {
  padding: toRem(40) toRem(32);
}

.items {
  display: flex;
  .item {
    transition: 0.5s;
    &:hover {
      scale: 1.3;
    }
    & + .item {
      margin-left: toRem(28);
    }
  }
}

















/* 모바일 스타일 */
@media (max-width: 768px) {
  .menu {
    display: block !important; /* 버튼 보이기 */
    background-color: red;
  }

  .items {
    display: none ;
    background-color: red;
